*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --primary-color: #0b0b0b;
    --secondary-color: #ffffff;
    --tertiary-color: #ff6f61;

    --font-heading: 'Playfair Display', serif;
    --font-body: 'Source Sans 3', sans-serif;
    --font-size-base: 16px;
}

body{
    font-family: var(--font-body);
    font-weight: 800;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

h1, h2{
    font-family: var(--font-heading);
    font-weight: 600;
}

h3, h4{
    font-family: var(--font-heading);
    font-weight: 500;
}

footer{
    background-color: var(--primary-color);
    color: var(--secondary-color);
    text-align: center;
    padding: 10px 0;
    font-size: calc(var(--font-size-base) * 0.8); /* 12.8px */
}

nav{
    display: flex;
    align-items: center;
    gap: 20px;
    padding: 10px 20px;
    background-color: var(--primary-color);
}

nav button{
    background: none;
    border: none;
    color: var(--secondary-color);
    font-size: var(--font-size-base);
    cursor: pointer;
    padding: 10px 15px;
}

nav button:hover{
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 5px;
}

nav button:active{
    transform: scale(0.98);
}

#logo{
    width: 30px;
    height: 30px;
}

#heroName{
    font-size: calc(var(--font-size-base) * 1.5); /* 24px */
    color: var(--secondary-color);
    font-weight: bold;
    margin-right: auto;
}

#content{
    flex: 1;
    display: flex;
    background-color: var(--primary-color);
}


/* main page */
#main-page-container{
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_8___});
    background-size: cover;
    background-position: center;
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: var(--secondary-color);
    gap: 15px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}

#main-menu-button{
    padding: 10px 20px;
    font-size: calc(var(--font-size-base) * 1.2);  /* 19.2px */
    border: none;
    border-radius: 5px;
    background-color: rgba(0, 0, 0, 0.6);
    color: var(--secondary-color);
    cursor: pointer;
}

#main-menu-button:hover{
    background-color: rgba(0, 0, 0, 0.8);
}

#main-menu-button:active{
    transform: scale(0.98);
}

#main-heading{
    font-size: calc(var(--font-size-base) * 2); /* 32px */
}

#main-subheading{
    font-size: calc(var(--font-size-base) * 1.2); /* 19.2px */
    font-style: italic;
}


/* menu */
#menuContainer{

    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_9___});
    background-size: cover;
    background-position: center;
    flex: 1;
    color: var(--secondary-color);
    
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 175px;
    color: var(--secondary-color);
    padding: 30px;
}

.menu-header{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 30px;
    letter-spacing: 0.4px;
}

.card-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
}

.card{
    background-color: rgba(0, 0, 0, 0.6);
    border-radius: 10px;
    padding: 20px;
    width: 250px;
    text-align: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    cursor: pointer;
}

.card img{
    width: 100%;
    height: 170px;
    border-radius: 5px;
}

.menu-header h1{
    font-size: calc(var(--font-size-base) * 2.8); 
    text-shadow: 0 2px 6px rgba(0, 0, 0, 0.6);
}

.menu-header h2{
    font-size: calc(var(--font-size-base) * 1.8); 
    font-style: italic;
    text-shadow: 0 2px 6px rgba(0, 0, 0, 0.6);
}

.card h3{
    font-size: calc(var(--font-size-base) * 1.2); /* 19.2px */
}

.card p{
    font-size: calc(var(--font-size-base) * 1); /* 16px */
    flex: 1;
}

.price{
    font-size: calc(var(--font-size-base) * 1.1); /* 17.6px */
    font-weight: bold;
}

.card:hover{
    background-color: rgba(0, 0, 0, 0.8);
    transform: scale(1.02);
    transition: all 0.3s ease;
}

/* Find Us */

#find-container{
    flex: 1;
    background-color: var(--primary-color);
    color: var(--secondary-color);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 150px;
    padding: 30px;

    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_10___});
    background-size: cover;
    background-position: center;
}


.find-header{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    text-align: center;
    letter-spacing: 0.4px;
}

.find-header h1{
    font-size: calc(var(--font-size-base) * 2.8); 
    text-shadow: 0 2px 6px rgba(0, 0, 0, 0.6);
}

.find-header h2{
    font-size: calc(var(--font-size-base) * 1.8); 
    font-style: italic;
    text-shadow: 0 2px 6px rgba(0, 0, 0, 0.6);
}

.find-body{
    display: flex;
    gap: 50px;
    flex-wrap: wrap;
    justify-content: center;
}

.map-card{
    border: 4px solid var(--secondary-color);
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.7);
}

.map-card:hover{
    transform: scale(1.02);
    transition: all 0.3s ease;
    cursor: pointer;
}

.map-card img{
    display: block;
    aspect-ratio: 4 / 3;
    width: clamp(400px,70vw,600px);
    object-fit: cover;
    background-color: rgba(255, 255, 255, 0.05);
}

.contact-card{
    display: flex;
    flex-direction: column;
    gap: 15px;
    font-size: calc(var(--font-size-base) * 1.1);
    background: rgba(0, 0, 0, 0.55);
    border-radius: 12px;
    padding: 24px 28px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(4px);
}


.contact-card:hover {
    transform: scale(1.02);
    transition: all 0.3s ease;
    cursor: pointer;
}

.contact-card p{
    line-height: 1.5;
}

.contact-card h3, .contact-card h4{
    color: var(--tertiary-color);
}

