flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    color: var(--secondary-color);\n    gap: 15px;\n    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);\n}\n\n#main-menu-button{\n    padding: 10px 20px;\n    font-size: calc(var(--font-size-base) * 1.2);  /* 19.2px */\n    border: none;\n    border-radius: 5px;\n    background-color: rgba(0, 0, 0, 0.6);\n    color: var(--secondary-color);\n    cursor: pointer;\n}\n\n#main-menu-button:hover{\n    background-color: rgba(0, 0, 0, 0.8);\n}\n\n#main-menu-button:active{\n    transform: scale(0.98);\n}\n\n#main-heading{\n    font-size: calc(var(--font-size-base) * 2); /* 32px */\n}\n\n#main-subheading{\n    font-size: calc(var(--font-size-base) * 1.2); /* 19.2px */\n    font-style: italic;\n}\n\n\n/* menu */\n#menuContainer{\n\n    background-image: url('./assets/images/counter.webp');\n    background-size: cover;\n    background-position: center;\n    flex: 1;\n    color: var(--secondary-color);\n    \n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    gap: 175px;\n    color: var(--secondary-color);\n    padding: 30px;\n}\n\n.menu-header{\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    gap: 30px;\n    letter-spacing: 0.4px;\n}\n\n.card-container{\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: center;\n    gap: 20px;\n}\n\n.card{\n    background-color: rgba(0, 0, 0, 0.6);\n    border-radius: 10px;\n    padding: 20px;\n    width: 250px;\n    text-align: center;\n    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 10px;\n    cursor: pointer;\n}\n\n.card img{\n    width: 100%;\n    height: 170px;\n    border-radius: 5px;\n}\n\n.menu-header h1{\n    font-size: calc(var(--font-size-base) * 2.8); \n    text-shadow: 0 2px 6px rgba(0, 0, 0, 0.6);\n}\n\n.menu-header h2{\n    font-size: calc(var(--font-size-base) * 1.8); \n    font-style: italic;\n    text-shadow: 0 2px 6px rgba(0, 0, 0, 0.6);\n}\n\n.card h3{\n    font-size: calc(var(--font-size-base) * 1.2); /* 19.2px */\n}\n\n.card p{\n    font-size: calc(var(--font-size-base) * 1); /* 16px */\n    flex: 1;\n}\n\n.price{\n    font-size: calc(var(--font-size-base) * 1.1); /* 17.6px */\n    font-weight: bold;\n}\n\n.card:hover{\n    background-color: rgba(0, 0, 0, 0.8);\n    transform: scale(1.02);\n    transition: all 0.3s ease;\n}\n\n/* Find Us */\n\n#find-container{\n    flex: 1;\n    background-color: var(--primary-color);\n    color: var(--secondary-color);\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    gap: 150px;\n    padding: 30px;\n\n    background-image: url('./assets/images/contactus.webp');\n    background-size: cover;\n    background-position: center;\n}\n\n\n.find-header{\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    gap: 20px;\n    text-align: center;\n    letter-spacing: 0.4px;\n}\n\n.find-header h1{\n    font-size: calc(var(--font-size-base) * 2.8); \n    text-shadow: 0 2px 6px rgba(0, 0, 0, 0.6);\n}\n\n.find-header h2{\n    font-size: calc(var(--font-size-base) * 1.8); \n    font-style: italic;\n    text-shadow: 0 2px 6px rgba(0, 0, 0, 0.6);\n}\n\n.find-body{\n    display: flex;\n    gap: 50px;\n    flex-wrap: wrap;\n    justify-content: center;\n}\n\n.map-card{\n    border: 4px solid var(--secondary-color);\n    border-radius: 10px;\n    overflow: hidden;\n    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.7);\n}\n\n.map-card:hover{\n    transform: scale(1.02);\n    transition: all 0.3s ease;\n    cursor: pointer;\n}\n\n.map-card img{\n    display: block;\n    aspect-ratio: 4 / 3;\n    width: clamp(400px,70vw,600px);\n    object-fit: cover;\n    background-color: rgba(255, 255, 255, 0.05);\n}\n\n.contact-card{\n    display: flex;\n    flex-direction: column;\n    gap: 15px;\n    font-size: calc(var(--font-size-base) * 1.1);\n    background: rgba(0, 0, 0, 0.55);\n    border-radius: 12px;\n    padding: 24px 28px;\n    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);\n    backdrop-filter: blur(4px);\n}\n\n\n.contact-card:hover {\n    transform: scale(1.02);\n    transition: all 0.3s ease;\n    cursor: pointer;\n}\n\n.contact-card p{\n    line-height: 1.5;\n}\n\n.contact-card h3, .contact-card h4{\n    color: var(--tertiary-color);\n}\n\n"],"sourceRoot":""}]);
